<template>
    <div>      
        <v-skeleton-loader type="card-heading, list-item-three-line" class="skeleton mx-auto" max-width="500" max-height="400" v-if="isFetchingUserData"></v-skeleton-loader>
        <v-container v-else class="container" fluid>
            <v-text-field
            v-model.trim="searchKeyword"
            solo
            label="Search"
            placeholder="Search"
            @keyup.enter="addSearchKeyword"
            class="container__searchbox"
            maxlength="5"
            />            
            <v-row class="container__chips" v-if="showSearchedKeywords">
                <v-btn v-for="(keyword, index) in store.getKeywords"
                :key="index"
                color="#FFCC80"
                class="container__chips__chip"
                @click="store.removeSearchedKeyword(keyword)"
                >
                    {{ keyword }}
                    <v-icon 
                    right 
                    size="22"
                    class="container__chips__chip__icon"
                    @click.stop="store.removeSearchedKeyword(keyword)"
                    >
                    mdi-close
                    </v-icon>
                </v-btn>
            </v-row>
            <table class="table" v-if="filteredUserData?.length">
                <thead class="table__head">
                <th class="table__head__data">S.No</th>
                <th class="table__head__data">Name</th>
                <th class="table__head__data">see user data</th>
                </thead>
                <tbody class="body">
                    <tr v-for="(user, key) in filteredUserData" :key="user.id" class="body__row">
                        <td class="body__row__data">{{ key + 1 }}</td>                       
                        <td class="body__row__data" v-if="showSearchedKeywords">
                            <span v-for="(words, index) in highlightSearchedKeyword(user.name)"
                            :key="index"
                            :class="words.highlight ? 'body__row__data--selected' : ''"
                            >{{ words.name }}</span>
                        </td>
                        <td class="body__row__data" v-else>
                            {{ user.name }}
                        </td>
                        <td class="body__row__data">
                            <button class="body__row__data__button" @click="setIndividualUserData(user)">Check Details</button>
                        </td>
                    </tr>
                </tbody>
            </table>
            <h1 v-else>No Results Found</h1>
        </v-container>
    </div>
</template>

<script setup lang="ts">
import { ref, onMounted, computed } from 'vue'
import axios from 'axios'

import router from '../router/index'
import { useUserDetailsStore } from '@/store'
import { User } from '@/types/Type'

const store = useUserDetailsStore();
const isFetchingUserData = ref(true);
const searchKeyword = ref<string>('')
const showSearchedKeywords = ref<boolean>(true)

const setIndividualUserData = (user:User) => {
    store.setIndividualUserData(user)
    store.setAccessToIndividualUserData(true)
    router.replace('/userDetails')
}

const lastSearchedKeyword = computed(() => {
    return store.getLastKeyword
})

const filteredUserData = computed(() => {
    const key = lastSearchedKeyword.value
    return store.userData?.filter((user) => {
        return user.name.toLowerCase().includes(key);
    })
})

const addSearchKeyword = () => {
    if(lastSearchedKeyword.value !== searchKeyword.value && searchKeyword.value !== '') {        
        store.addSearchedKeyword(searchKeyword.value)
    }
    searchKeyword.value = ''
}

const highlightSearchedKeyword = (name: string) => {
    const keyword = lastSearchedKeyword.value;
    const startIndex = name.toLowerCase().lastIndexOf(keyword.toLowerCase());
    let splittedWords = []
    splittedWords.push({name: `${name.slice(0, startIndex)}`, highlight: false})
    splittedWords.push({name: `${name.slice(startIndex, startIndex + keyword.length)}`, highlight: true})
    splittedWords.push({name: `${name.slice(startIndex + keyword.length)}`, highlight: false})

    return splittedWords
}

onMounted(async () => {   
    const response = await axios({
        method: 'get',
        url: 'https://jsonplaceholder.typicode.com/users'
    })   
    store.setUserData(response.data)
    const loadingTime = setTimeout(() => {
        isFetchingUserData.value = false;
        clearTimeout(loadingTime);
    }, 300)
})
</script>

